.page-header {
  padding-top: 33px;
  height: 750px;
  background-image: url("/img/header-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: #2e333a;
}
.page-header__menu-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 200px;
  margin-right: 45px;
}
.page-header__menu {
  position: relative;
}
.page-header__menu-title {
  text-transform: uppercase;
}
.page-header__title {
  margin: 0;
  padding: 0;
  margin-bottom: 30px;
  font-size: 36px;
  line-height: 40px;
  font-weight: bold;
  text-align: center;
}
.page-header__info {
  margin: 0;
  margin-bottom: 30px;
  padding: 0 10%;
  line-height: 25px;
  font-weight: lighter;
  text-align: center;
}
.page-header__menu-button {
  position: absolute;
  z-index: 2;
  margin-left: 20px;
  margin-top: 5px;
  width: 25px;
  height: 19px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.page-header__menu-button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 2px;
  background-color: white;
  box-shadow: 0 6px 0 0 white, 0 12px 0 0 white;
}
.page-header__button {
  margin: 0 auto;
}
.button {
  display: block;
  padding: 15px 25px;
  font-size: 13px;
  font-weight: bold;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 40px;
  background-color: #29323c;
}
.button--transparent {
  background-color: transparent;
  border: 1px white solid;
}
.button--sign-up {
  padding-left: 33px;
  padding-right: 33px;
}
.services {
  position: relative;
  margin: 0;
  padding: 95px 0;
  background-color: #f5f5f5;
}
.services::before {
  content: "";
  position: absolute;
  left: 50%;
  top: -154px;
  margin-left: -25px;
  width: 50px;
  height: 154px;
  background-image: url("/img/arrow.png");
}
.services__title {
  margin-bottom: 80px;
}
.block-title__title {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  font-size: 26px;
  line-height: 22px;
  font-weight: bolder;
  text-transform: uppercase;
  color: #29323c;
  text-align: center;
}
.block-title__description {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 30px;
  color: #29323c;
  text-align: center;
}
.block-title__description::after {
  content: "";
  position: absolute;
  top: 143%;
  left: 50%;
  margin-left: -20px;
  width: 40px;
  height: 2px;
  background-color: #29323c;
}
.block-title--white .block-title__title {
  color: #f2f2f2;
}
.block-title--white .block-title__description {
  color: #f2f2f2;
}
.block-title--white .block-title__description::after {
  background-color: #f2f2f2;
}
.futures-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;
}
.futures-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: 250px;
  margin-bottom: 20px;
}
.futures-list__item-title {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 36px;
  font-weight: bold;
  color: #29323c;
  text-align: center;
  text-transform: uppercase;
}
.futures-list__item-description {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 22px;
  color: #29323c;
  text-align: center;
}
.feedbacks {
  padding-top: 100px;
  padding-bottom: 100px;
}
.feedbacks__title {
  margin: 0;
  padding: 0;
  margin-bottom: 75px;
}
.feedbacks__text {
  margin: 0;
  margin-bottom: 69px;
  padding: 0;
  font-size: 16px;
  line-height: 26px;
  color: #29323c;
  text-align: center;
}
.feedbacks__auhtor-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.feedbacks__auhtor-wrapper::after {
  content: "";
  position: absolute;
  left: 50%;
  top: -40px;
  margin-left: -8px;
  width: 16px;
  height: 11px;
  background-image: url("/img/comma.png");
}
.feedbacks__photo {
  margin-bottom: 13px;
}
.feedbacks__name {
  font-size: 15px;
  line-height: 20px;
  font-weight: bold;
  color: #29323c;
}
.feedbacks__profession {
  position: relative;
  font-size: 13px;
  line-height: 20px;
  color: #29323c;
}
.feedbacks__profession::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -40px;
  margin-left: -27px;
  width: 55px;
  height: 15px;
  background-image: url("/img/ellipses.jpg");
}
.team {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 90px 0;
  background-image: url("/img/team-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: #2e333a;
}
.team__title {
  margin-bottom: 100px;
}
.team__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 95px;
}
.team__list-item {
  margin-bottom: 20px;
}
.team__list-item:last-child {
  margin-right: 0;
}
.team__button {
  margin: 0 auto;
}
.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.social__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 6px;
}
.social__item:last-child {
  margin-right: 0;
}
.worker-card {
  display: flex;
  flex-direction: column;
  flex-basis: 270px;
  padding: 0;
  background-color: rgba(242, 242, 242, 0.75);
}
.worker-card__photo {
  margin-bottom: 30px;
}
.worker-card__container {
  padding: 30px 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  justify-content: space-between;
}
.worker-card__name {
  margin: 0;
  margin-bottom: 7px;
  padding: 0;
  font-size: 15px;
  line-height: 20px;
  font-weight: 800;
  color: #29323c;
  text-align: center;
}
.worker-card__profession {
  margin: 0;
  margin-bottom: 23px;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
  color: #29323c;
}
.worker-card__social {
  margin: 0 5%;
}
.clients {
  background-color: #f5f5f5;
}
.clients__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding: 40px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}
.clients__list-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (max-width: 480px) {
  .clients {
    display: block;
  }
  .clients__list-item {
    margin-bottom: 20px;
  }
}
.price {
  margin: 0;
  padding: 100px 0px;
}
.price__title {
  margin-bottom: 95px;
}
.price__cards-wrapper {
  margin-top: 100px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 40px 160px auto;
  grid-template-areas: ". area2 ." "area1 area2 area3" "area1 area2 area3";
}
.price__card {
  align-items: end;
  border: 1px solid rgba(41, 50, 60, 0.5);
}
.price__card:first-child {
  border-right: none;
}
.price__card:last-child {
  border-left: none;
}
.price__card--promo {
  border-top: 5px solid #29323c;
  margin-top: -4px;
}
.price__card-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
  border-bottom: 1px solid rgba(41, 50, 60, 0.5);
}
.price__card-plan {
  margin: 0;
  padding: 0;
  margin-bottom: 15px;
  font-size: 20px;
  line-height: 36px;
  font-weight: bold;
  color: #29323c;
}
.price__card-price {
  margin: 0;
  padding: 0;
  margin-bottom: 5px;
  font-size: 45px;
  color: #29323c;
}
.price__card-period {
  margin: 0;
  padding: 0;
  font-size: 14px;
  color: rgba(41, 50, 60, 0.8);
  text-transform: uppercase;
  font-weight: lighter;
}
.price__card-footer {
  margin: 0;
  padding: 60px 18% 35px 18%;
}
.price__card-description {
  margin: 0;
  padding: 0;
  margin-bottom: 20%;
  font-size: 14px;
  line-height: 40px;
  font-weight: bold;
  color: #29323c;
  text-align: center;
}
.price__card-button {
  margin: 0 auto;
}
.grid-column {
  border: 1px solid red;
}
.grid-column1 {
  grid-area: area1;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 160px auto;
  grid-template-areas: "header" "footer";
}
.column1-header {
  grid-area: header;
}
.column1-footer {
  grid-area: footer;
}
.grid-column2 {
  grid-area: area2;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 200px auto;
  grid-template-areas: "header" "footer";
}
.column2-header {
  grid-area: header;
}
.column2-footer {
  grid-area: footer;
}
.grid-column3 {
  grid-area: area3;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 160px auto;
  grid-template-areas: "header" "footer";
}
.column3-header {
  grid-area: header;
}
.column3-footer {
  grid-area: footer;
}
@media (max-width: 480px) {
  .price__cards-wrapper {
    display: block;
  }
  .price__card:first-child {
    border-right: 1px solid rgba(41, 50, 60, 0.5);
  }
  .price__card:last-child {
    border-left: 1px solid rgba(41, 50, 60, 0.5);
  }
  .price__card--promo {
    border-top: none;
    margin-top: 0;
  }
}
.contact {
  display: flex;
  flex-direction: row;
  height: 649px;
}
.contact__map-wrapper {
  width: 50%;
}
.contact__map {
  width: 100%;
}
.contact__form {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  padding: 112px 7% 112px 7%;
  width: 50%;
  background-image: url("/img/form-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: #2e333a;
}
.contact__form-group {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.contact__form-input {
  margin-left: 7%;
  width: 300px;
  height: 45px;
  background: transparent;
  border: 1px solid white;
}
.contact__form-input--textarea {
  height: 144px;
}
.contact__form-label--message {
  margin-bottom: 128px;
}
.contact__form-submit {
  width: 120px;
  height: 45px;
  color: white;
  background: transparent;
  border: 1px solid white;
}
@media (max-width: 768px) {
  .contact {
    display: block;
    height: auto;
  }
  .contact__form {
    width: auto;
    padding-right: 15%;
  }
  .contact__form-group {
    margin-bottom: 10px;
  }
  .contact__map-wrapper {
    width: auto;
  }
  .contact__form-label--message {
    margin-bottom: 0;
  }
}
@media (max-width: 480px) {
  .contact__form {
    padding: 112px 3% 112px 3%;
  }
  .contact__form-group {
    flex-wrap: wrap;
  }
  .contact__form-input {
    margin-left: 0;
  }
}
.page-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 50px 0;
  background-color: #f5f5f5;
}
.page-footer__logo-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.page-footer__logo {
  margin-right: 25px;
}
.page-footer__copyright {
  font-size: 14px;
  line-height: 22px;
  color: #29323c;
}
.page-footer__social {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.page-footer__social-slogan {
  width: 100%;
  font-size: 14px;
  line-height: 22px;
  color: #29323c;
}
.works__title {
  margin-bottom: 80px;
  padding-top: 80px;
}
.works__galery {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  background-color: black;
}
html {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Montserat", "Arial", sans-serif;
  font-size: 17px;
  line-height: 25px;
  font-style: normal;
  color: #e8e8e8;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  padding-left: 13%;
  padding-right: 13%;
}
